.hero {
    padding: 30px 36px 30px 126px;
    display: flex;
    justify-content: space-between; 
    align-items: center;
    background-color: #F8F8F8;
    background-image: url(../../../public/hero/emerald.svg);
    background-size: auto 120%;
    background-repeat: no-repeat;
    background-position: left -20px% top 24px;
    height: 400px;
}

.hero__picture-wrapper {
    flex-basis: 45%;
}
   
.hero-wrapper__subtitle {
    font-weight: 400;
    font-size: 20px;
    line-height: 100%;
    margin-bottom: 30px;
}

.hero-wrapper__title {
    margin-bottom: 54px;
}


@media (max-width: 900px) {

    .hero-wrapper__subtitle {
        margin-bottom: 16px;
    }

    .hero-wrapper__title {
        margin-bottom: 24px;
    }

    .hero {
        padding: 12px 26px 0 96px;
        height: 300px;
        
    }


}

@media (max-width: 650px) {
   
    .hero {
        padding: 25px 26px 30px 70px;
    }

    .hero-wrapper__subtitle {
        font-size: 16px;
    }

} 

@media (max-width: 460px) {

    
    .hero {
        flex-direction: column-reverse;
        justify-content: center;
        padding: 10px 20px 40px 20px;
        gap: 20px;
    }

    .hero__picture-wrapper {
        flex-basis: auto;
    }

    .hero__img {
        max-height: 250px;
    }

    .hero {
        height: auto;
    }
    
    .hero__img{
        max-height: auto;
    }

}